@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";
@import "../../node_modules/bootstrap/scss/bootstrap-grid.scss";

$menu-margin: 45px;

* {
    box-sizing: border-box; }


body {
    font-family: TT Norms, sans-serif;
    font-size: 18px;
    color: #111; }



ul {
    padding: 0;
    margin: 0;
    list-style: none; }

.nav-btn {
    padding: 23px 25.5px;
    line-height: 0;
    border: 2px solid #54B125;
    border-radius: 50px;
    font-size: 14px;
    background: transparent;
    transition: .3s;
    &:hover, &:active, &:focus {
        outline: 0;
        background-color: #54B125;
        color: #fff;
        transition: .3s; } }

.section-title {
    padding: 0 15px;
    text-align: center;
    font-weight: bold;
    font-size: 32px;
    line-height: 38px;
    text-transform: uppercase;
    margin-bottom: 12px; }


.section-short-desc {
    line-height: 21px;
    text-align: center; }

.icon-bread {
    width: 6px;
    height: 10px;
    margin: 0 12px; }

.bread-link {
    font-size: 12px;
    line-height: 14px;
    color: #A5A5A5; }

a.bread-link {
    transition: .3s;
    &:hover, &:active, &:focus {
        color: #54B125;
        text-decoration: none;
        transition: .3s; } }

span.bread-link {
    color: #111; }

.bread-crumbs {
    margin: 30px auto; }

.single-page-title {
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 30px; }

@import "../fonts/stylesheet.css";
@import "includes/header.sass";
@import "includes/menu.sass";
@import "includes/first-section.sass";
@import "includes/advantages.sass";
@import "includes/products.sass";
@import "includes/builds.sass";
@import "includes/about.sass";
@import "includes/delivery.sass";
@import "includes/contacts.sass";
@import "includes/catalog.sass";
@import "includes/decorations.sass";
@import "magnific-popup.css";
@import "includes/footer.sass";

.single-page-menu {
    background: #fff;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05); }
