.catalog-section {
    padding-bottom: 100px;
    position: relative;
    .single-page-title {
        margin-bottom: 40px; } }

.catalog-table {
    border: 1px solid #EDEDED;
    width: 100%; }

.table-row {
    transition: .2s;
    &:not(.title-row) {
        &:hover, &:focus, &:active {
            transition: .2s;
            background: #f4f4f4; } } }

.table-title {
    border: 1px solid #EDEDED;
    border-bottom: 2px solid #54B125;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    vertical-align: middle;
    padding: 15px 15px;
    white-space: nowrap; }

.table-cell {
    border: 1px solid #EDEDED;
    font-size: 13px;
    line-height: 15px;
    text-align: center;
    padding: 12px 10px;
    white-space: nowrap; }

.price-desc-container {
    padding-top: 40px;
    border: 1px solid #EDEDED;
    border-top: none;
    margin-bottom: 45px; }

.price-desc {
    font-weight: 500;
    font-size: 12px;
    line-height: 148.52%;
    text-align: center;
    max-width: 620px;
    margin: 0 auto 40px; }

.catalog-nav-items {
    border: 1px solid #EDEDED; }

.catalog-nav-item:not(:nth-last-child(1)) {
    border-bottom: 1px solid #EDEDED; }

.catalog-nav-link {
    display: block;
    height: 100%;
    width: 100%;
    padding: 16px 20px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #111;
    text-transform: uppercase;
    transition: .2s;
    &:not(.catalog-active-link) {
        &:hover, &:focus, &:active {
            background: #F4F4F4;
            color: #111;
            text-decoration: none;
            transition: .2s; } } }

.catalog-active-link {
    background: #54B125;
    color: #fff;
    &:hover, &:focus, &:active {
        color: #fff;
        text-decoration: none; } }
