.delivery-section {
    position: relative; }

.delivery-desc {
    max-width: 770px;
    font-size: 16px;
    line-height: 22px; }

.delivery-items {
    padding-top: 40px; }

.delivery-item {
    display: flex;
    margin-bottom: 70px; }

.delivery-img {
    box-shadow: 0px 7px 20px rgba(0, 0, 0, 0.15);
    img {
        width: 100%;
        height: auto; } }

.delivery-item-header {
    display: flex;
    align-items: center;
    margin-bottom: 33px; }

.delivery-item-title {
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #54B125;
    text-transform: uppercase;
    margin: 0;
    background-image: url("data:image/svg+xml,%3Csvg width='115' height='46' viewBox='0 0 115 46' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M-1.00536e-06 23L15 3.08141L15 42.9186L-1.00536e-06 23Z' fill='%23F4F4F4'/%3E%3Cpath d='M15 3H112C113.657 3 115 4.34315 115 6V40C115 41.6569 113.657 43 112 43H15V3Z' fill='%23F4F4F4'/%3E%3C/svg%3E%0A");
    border-radius: 0px 3px 3px 0px;
    width: 115px;
    height: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 10px; }

.delivery-item-title-desc {
    margin: 0 0 0 20px; }

.delivery-desc-item {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 10px; }

.delivery-contacts {
    padding-bottom: 60px; }

.delivery-contacts-title {
    font-weight: bold;
    font-size: 24px;
    line-height: 136.52%;
    color: #54B125;
    margin-bottom: 20px; }

.delivery-phone a {
    font-weight: bold;
    font-size: 20px;
    line-height: 136.52%;
    color: #111;
    &:hover, &:active, &:focus {
        color: #111;
        text-decoration: none; } }
