.about-section {
    position: relative; }

.about-conten {
    display: flex;
    margin: 0 -15px; }

.about-text-conten {
    width: 50%;
    padding: 0 15px; }

.about-image {
    width: 50%;
    padding: 0 15px;
    img {
        height: auto;
        width: 100%; } }

.about-text-conten p {
    font-size: 16px;
    line-height: 23px;
    margin-bottom: 30px; }

.signature-container {
    display: flex;
    justify-content: flex-end;
    align-items: center; }

.signature-desc {
    margin-right: 20px; }

p.signature-name {
    margin-bottom: 5px;
    text-align: right;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px; }

p.signature-position {
    margin: 0;
    text-align: right;
    font-size: 14px;
    line-height: 17px; }
