.contacts-items {
    padding-bottom: 90px; }

.contats-item {
    border: 2px solid #F5F5F5;
    margin-bottom: 50px; }

.contats-item-row {
    min-height: 460px; }

.contacts-item-content {
    padding-top: 50px;
    padding-bottom: 30px; }

.contacts-item-title {
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    margin-bottom: 40px; }

.contacts-block {
    padding-left: 40px;
    margin-bottom: 20px; }

.contacts-item-icon {
    height: 15px;
    width: 15px;
    margin-right: 15px; }

.contacts-item-subtitle {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase; }

.contacts-item-desc,
a.contacts-item-desc {
    padding-left: 30px;
    font-size: 12px;
    line-height: 14px;
    color: #000;
    margin-bottom: 5px;
    display: block; }

a.contacts-item-desc {
    &:hover, &:active, &:focus {
        color: #000;
        text-decoration: none; } }

.bordered-footer {
    border: 1px solid #D8D8D8; }
